			 watchvalue(e){
				 var title="";	 
				 return new Promise(function(resolve){
					 setTimeout(function(){
						 if(e==1){
						 					title="非常差" 
						 }else if(e==2){
						 					 title="很差"
						 					 }else if(e==3){
